.profilecard {
  width: 400px;
  text-align: center;
  background-color: #fff;
  border-radius: 6px;
  margin: 120px auto;
  padding: 0 20px 20px;
  box-shadow: 0 2px 5px #00000013;
}

.profilecard:hover .avatar-flip {
  transform: rotateY(180deg);
}

.profilecard:hover .avatar-flip img:first-child {
  opacity: 0;
}

.profilecard:hover .avatar-flip img:last-child {
  opacity: 1;
}

.avatar-flip {
  height: 150px;
  width: 150px;
  box-shadow: 0 0 0 13px #230357;
  -webkit-box-shadow: 0 0 0 13px #ff009d;
  border-radius: 100px;
  margin: auto;
  transition: all .3s ease-in-out;
  position: relative;
  top: -60px;
  overflow: hidden;
  -moz-box-shadow: 0 0 0 13px #030c80;
}

.avatar-flip img {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  border-radius: 100px;
  transition: all .3s ease-in-out;
  animation: gradient 15s infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.avatar-flip img:first-child {
  z-index: 1;
}

.avatar-flip img:last-child {
  z-index: 0;
  opacity: 0;
  transform: rotateY(180deg);
}

h2 {
  color: #333;
  margin-bottom: 15px;
  font-size: 32px;
  font-weight: 600;
}

h4 {
  color: #ff009d;
  letter-spacing: 1px;
  margin-bottom: 25px;
  font-size: 13px;
}

p {
  color: #666;
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 26px;
}

#buttonprf {
  cursor: pointer;
  background: none;
  border: none;
}

